enum TokenType {
  Number,
  Operator,

  LeftParen,
  RightParen,
  LeftBracket,
  RightBracket,
  LeftBrace,
  RightBrace,

  String,
  HexNumStr,
  BinNumStr,
  OctNumStr,

  Vector,
  List,

  Unknown,
  EOF
}

enum StackType {
  Number,
  String,
  Vector,
  List,
  Unknown
}

class Token {
  constructor(
    public type: TokenType,
    public value: string
  ) { }
}

export { TokenType, StackType, Token }